@import "help/vars_and_mixins";

.contacts-page {
  background: none;
  .main {
    overflow: hidden;
    position: relative;

    &-offices {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      &-title {
        padding: 120px 0 10px 0 ;
        max-width: 540px;
        font-family: TitilliumWebBold;
        @include font ($weight: 600, $size: 24px, $line-height: 1.58, $letter-spacing: normal, $color: $black);
        text-align: center;
      }
      &-text {
        font-family: TitilliumWebSemiBold;
        max-width: 540px;
        @include font ($weight: 600, $size: 18px, $line-height: 1.58, $letter-spacing: normal, $color: $black);
        text-align: center;
        padding-bottom: 280px;
        @media screen and (max-width: 800px) {
          padding-bottom: 80px;
        }
        @media screen and (max-width: 534px) {
          padding-bottom: 20px;
        }
      }
      &-list {
        text-align: left;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        &__item {
          margin: 60px 0px 20px 0px;
          width: 33.3%;
          @media screen and (max-width: 800px) {
            margin: 40px 0px 10px 0px;
            width: 50%;  
          }
          @media screen and (max-width: 534px) {
            margin: 20px 0px 0px 0px;
            width: 100%;  
          }
          min-width: 240px;
          height: 200px;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: center;
          &-card {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            width: 220px;
            &-name {
              font-family: TitilliumWebRegular;
              @include font ($weight: normal, $size: 24px, $line-height: 32px, $letter-spacing: normal, $color: $black);
            }
            &-town {
              font-family: TitilliumWebBold;
              @include font ($weight: normal, $size: 24px, $line-height: 32px, $letter-spacing: normal, $color: $black);
              padding-bottom: 20px;
            }
            &-location, &-phone, &-mail {
              font-family: TitilliumWebRegular;
              @include font ($weight: normal, $size: 14px, $line-height: 24px, $letter-spacing: normal, $color: $black); 
              a {
                color: $black;
              } 
              max-width: 220px;
            }
          }
        }
      }
    }

    &-callback {
      position: relative;
      height: 420px;
      background: url(../img/contacts-asnw-desctop.jpg) no-repeat center/cover;
      @media screen and (max-width:768px) {
        background: url(../img/contacts-asnw-tablet.jpg) no-repeat center/cover;
      }
      @media screen and (max-width:480px) {
        height: 245px;
        background: url(../img/contacts-asnw-mobile.jpg) no-repeat center/cover;
      }
      &-answ {
        position: absolute;
        width: 515px;
        height: 350px;
        top: -140px;
        right: 0;
        padding-top: 100px;
        padding-left: 50px;
        border-radius: 18px 0 0 18px;
        background-color: $sunyellow;
        @media screen and (max-width: 800px) {
          width: 515px;
          height: 300px; 
          padding-top: 50px;
        }
        @media screen and (max-width: 534px) {
          width: 305px;
          height: 300px; 
          &-text {
            width: 240px;
            font-size: 18px;
          }
        }
        &-text {
          position: absolute;
          z-index: 2;
          width: 300px;
          font-family: TitilliumWebSemiBold;
          font-size: 24px;
          font-weight: 600;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.58;
          letter-spacing: normal;
          color: $black;
          @media screen and (max-width: 534px) {
            font-size: 18px;
          }
        }
        &-img {
          position: absolute;
          margin-left: 200px;
          margin-top: 90px;
        }
      }
    }

    &-feedback {
      position: relative;
      background-color: $black;
      padding-top: 120px;
      padding-bottom: 130px;
      @media screen and (max-width: 900px) {
        padding-top: 60px;
        padding-bottom: 60px;
      }
      &-title {
        position: relative;
        font-family: TitilliumWebSemiBold;
        @include font ($weight: normal, $size: 40px, $line-height: 52px, $letter-spacing: normal, $color: $white);
        text-align: center;
        padding-bottom: 60px;
        z-index: 1;
        @media screen and (max-width: 899px) {
          display: none;
        }
      }
      &-container {
        z-index: 1;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: space-between;
        @media screen and (max-width: 900px) {
          flex-direction: column;
          justify-content: space-between;
          align-items: space-between;  
        }
        &-text {
          z-index: 1;
          font-family: TitilliumWebRegular;
          @include font ($weight: normal, $size: 18px, $line-height: 28px, $letter-spacing: normal, $color: $browngrey);
          max-width: 350px;
          text-align: left;
          margin-right: 50px;
          &-title {
            font-family: TitilliumWebSemiBold;
            @include font ($weight: normal, $size: 40px, $line-height: 52px, $letter-spacing: normal, $color: $white);
            text-align: left;
            @media screen and (min-width: 900px) {
              display: none;
            }
          }
        }
        ul { 
          z-index: 1;
          max-width: 635px; 
          min-width: 580px;
          @media screen and (max-width: 900px) {
            max-width: 100%; 
          }
          @media screen and (max-width: 640px) {
            max-width: 100%; 
            min-width: 100%; 
          }
          
        }
        .main-feedback-container-form {
          p {
            span { 
              width: 285px; 
              @media screen and (max-width: 640px) {
                width: 100%; 
              }
            }
            span:nth-child(5) {
              width: 100%;
              .main-feedback-container-form-p-input {
                min-width: 100%;
              }
            }
            span:nth-child(6) {
              width: 100%;
            }
          }
        }
        &-form {
          p {
            z-index: 1;
            position: relative;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;

            span input {
              min-width: 285px;
              @media screen and (max-width: 640px) {
                min-width: 100%; 
              }
              background: transparent;
              border: 0px;
              border-bottom: 1px solid $greyishbrown;
              font-family: "TitilliumWebRegular", serif;
              font-size: 14px;
              line-height: 1.79;
              color: #ffffff;
              margin-bottom: 40px;
              padding: 14px;
            }
            span input:hover {
              border-bottom: 1px solid $verylightpink;
            }
            span input:focus {
              outline: none;
            }
            span.main-feedback-container-form-p-agreetment-text {
              font-family: TitilliumWebRegular;
              @include font ($weight: normal, $size: 12px, $line-height: 16px, $letter-spacing: normal, $color: $browngrey);
              padding-bottom: 40px;
            }
            span.upl-subm {
              position: relative;
              display: flex;
              flex-direction: row-reverse;
              align-items: center;
              justify-content: space-between;
              width: 100%;
              height: 55px;
              .btn-wrapper {
                display: block;
                width: 285px;
                height: 100%;
                position: relative;
                // padding: 0 24px;
                .button-subm {
                  font-family: "TitilliumWebSemiBold",serif;
                  font-size: 14px;
                  // padding-right: 40px;
                  @include flex (center, center, row);
                  // padding: 0 24px;
                  background-color: $sunyellow;
                  border-radius: 36px;
                  border: solid 2px $sunyellow;
                  color: $black;
                  cursor: pointer;
                  width: 200px;
                  height: 55px;         
                  &__arrow {
                    position: absolute;
                    background-image: url(../img/arrow-up-right-black.svg);
                    width: 19px;
                    height: 8px;
                    top:23px;
                    right: 24px;
                  }
                  width: 200px;
                }
              }
              
              .inputfile {
                display: block;
                width: 0.1px;
                height: 0.1px;
                opacity: 0;
                overflow: hidden;
                position: absolute;
                z-index: -1;

                &-label {
                  padding-left: 6px;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  img {
                    padding-right: 8px;
                  }
                  span {
                    font-family: TitilliumWebRegular;
                    @include font ($weight: normal, $size: 14px, $line-height: 24px, $letter-spacing: normal, $color: $browngrey);
                  }
                }
              }
              
            }  
          }         
        }
      }
    }
  }
  svg#worldmapOffices {
    z-index: -1;
  }
  svg#worldmapFeedback {
    z-index: 0;
  }
}

